$text-font-size: 40px;
$text-line-height: 50px;

h1 {
  font-size: $text-font-size * 2;
  line-height: $text-line-height * 2;
}

h2 {
  font-size: $text-font-size * 1.8;
  line-height: $text-line-height * 1.8;
}

h3 {
  font-size: $text-font-size * 1.4;
  line-height: $text-line-height * 1.4;
}

h4 {
  font-size: $text-font-size * 1.2;
  line-height: $text-line-height * 1.2;
}

h1,
h2,
h3,
h4 {
  margin-bottom: 40px;
  font-weight: bold;
}

p {
  font-size: $text-font-size;
  line-height: $text-line-height;
}

div {
  margin-bottom: 40px;
  page-break-inside: avoid;
}

header {
  margin-bottom: 40px;
}
