// Gallery Image Positions

/* stylelint-disable */
.ce-textpic {
  &::after,
  .ce-gallery::after,
  .ce-center .ce-row::after {
    content: '';
    clear: both;
    display: block;
    visibility: hidden;
    height: 0;
  }

  .ce-gallery {
    // "Border around each element"
    &.ce-border {
      img {
        border: solid 1px grey;
      }
    }
  }

  // Above Center
  &.ce-center.ce-above {
    .ce-media {
      margin-bottom: 10px;

      img {
        display: block;
        margin: 0 auto;
      }
    }

    .ce-inner {
      text-align: center;
    }

    .ce-row {
      display: block;
    }

    .ce-column {
      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // Above Right
  &.ce-right.ce-above {
    .ce-media {
      margin-bottom: 10px;

      img {
        display: block;
      }
    }

    .ce-row {
      float: right;
    }

    .ce-column {
      float: left;

      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // Above Left
  &.ce-left.ce-above {
    .ce-media {
      margin-bottom: 10px;

      img {
        display: block;
      }
    }

    .ce-column {
      float: left;

      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // Below Center
  &.ce-center.ce-below {
    .ce-gallery {
      .ce-media {
        margin-top: 10px;

        img {
          display: block;
          margin: 0 auto;
        }
      }
    }

    .ce-inner {
      text-align: center;
    }

    .ce-row {
      display: block;
    }

    .ce-column {
      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // Below Right
  &.ce-right.ce-below {
    .ce-media {
      margin-top: 10px;

      img {
        display: block;
      }
    }

    .ce-row {
      float: right;
    }

    .ce-column {
      float: left;

      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // Below Left
  &.ce-left.ce-below {
    .ce-media {
      margin-top: 10px;

      img {
        display: block;
      }
    }

    .ce-column {
      float: left;

      & + .ce-column {
        .ce-media {
          margin-left: 10px;
        }
      }
    }
  }

  // In Text Left
  &.ce-left.ce-intext {
    .ce-gallery {
      float: left;

      .ce-media {
        margin-right: 10px;

        img {
          display: block;
        }
      }

      .ce-column + .ce-column {
        .ce-media {
          margin-top: 10px;
        }
      }
    }
  }

  // In Text Right
  &.ce-right.ce-intext {
    .ce-gallery {
      float: right;

      .ce-media {
        margin-left: 10px;

        img {
          display: block;
        }
      }

      .ce-column + .ce-column {
        .ce-media {
          margin-top: 10px;
        }
      }
    }
  }

  // Beside Text
  &.ce-nowrap {
    .ce-bodytext {
      overflow: hidden;
    }
  }
}

/* stylelint-enable */
